body {
    margin : 0;
    background: #D8EEFE;
}
header {
    max-width: 90%;
    margin: auto;
    margin-top: 63px;
}
h1 {
    margin-top: 0;
    font-family: Rubik;
    font-style: normal;
    font-weight: bold;
    font-size: 64px;
    color: #094067;
}

.container {
    display: flex;
    flex-direction: column;
    max-width: 90%;
    margin: auto;
}
.inputSearch {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.filter-month,
.filter-name {
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #094067;
    display: flex;
    flex-direction: column;
    gap: 13px;
    position: relative;
}
.filter-month-select {
    -webkit-appearance: none;
    background-image: url('/images/Vector.svg');
    background-repeat: no-repeat;
    background-position: 90%;
    padding: 20px;
    border-style: none;
    width: 282px;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
 }
 
.filter-name-input {
    background: #FFFFFE;
    border-radius: 5px;
    border-style: none;
    padding: 20px;
    width: 264px;
}

.list-wrapper {
    padding-inline-start: 0;
    margin-block-start: 1rem;
}
.list-of-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-block-start: 31px;
    background: #FFFFFE;
    border-radius: 5px;
    list-style: none;
    padding-block-start: 18px;
    padding-block-end: 19px;
}
.picture {
    flex-grow: 0.3;
}
.names-and-date {
    flex-grow: 1.5;
    text-align: center;
}

.firstName,
.lastName {
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
    color: #000000;
}
.date,
.birthday-in-days {
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: #5F6C7B;
    margin: 0;
}
.names {
    text-align: center;
}
.future-age {
    color: #EF4565;
}
.group-btn {
    flex-grow: 0.2;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.edit, .delete {
    border-style: none;
    background: none;
}

.add {
    background: #EF4565;
    border-radius: 5px;
    color: #FFFFFE;
    width: 158px;
    border-style: none;
    border-style: 0;
    margin-top: 34px;
    padding: 20px;
 }
 img {
    width: 92px;
    height: 94px;
    border-radius: 5px;
}

button {
    cursor: pointer;
}

.edit-close-btn,
.add-close-btn {
    right: 5%;
    position: absolute;
    background: none;
    border-style: none;
}
.delete-close-btn {
    position: absolute;
    top: 10%;
    right: 5%;
    background: none;
    background: none;
    border-style: none;
}

.to-deleteEl p {
    font-size: 2rem;
}

.to-deleteEl p:first-of-type {
    color: red;
}

.buttons {
    display: flex;
    gap: 1rem;
    border-radius: 6px;
}


.to-delete {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
}

.to-deleteEl {
    text-align: center;
    background: #fff;
    position: relative;
    max-width: 70%;
    top: 20%;
    margin: auto;
    padding: 1rem;
    border-radius: 6px;
}

.add-person ,
.to-edit {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.8);
}
.to-add,
.popup {
    position: relative;
    top: 10%;
    max-width: 90%;
    margin: auto;
    background: #fff;
    height: 80vh;
    overflow-y: auto;
}
.inner-popup {
    display: flex;
    flex-direction: column;
    max-width: 50%;
    padding: 2rem;
    margin: auto;
    gap: 1rem;
}
.add-title,
.person-name {
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 48px;
    line-height: 57px;
    color: #094067;
}
.add-input,
.edit-input {
    max-width: 264px;
    padding: 1rem;
    background: #D8EEFE;
    border-radius: 6px;
    border-style: none;
}

#myInput {
    background-image: url('./images/searchicon.svg');
    /* Add a search icon to input */
    background-position: 0 2px;
    /* Position the search icon */
    background-repeat: no-repeat;
    /* Do not repeat the icon image */
    max-width: 300px;
    /* Full-width */
    font-size: 16px;
    /* Increase font-size */
    padding: 12px 20px 12px 40px;
    /* Add some padding */
    border: 1px solid #ddd;
    /* Add a grey border */
}
.remove,
.submit {
    background: #EF4565;
    border-radius: 5px;
    width: 158px;
    padding: 1rem;
    border-style: none;
} 

.cancel {
    background: #FFFFFF;
    border: 2px solid #5F6C7B;
    border-radius: 5px;
    width: 158px;
    padding: 1rem;
}

.add-label,
.edit-label {
    font-family: Rubik;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #094067;

}
svg {
    width: 40px;
}

@media (min-width: 800px) {
    header {
        max-width: 779px;
    }
    .container {
        max-width: 779px;
        margin: auto;
    }
    .inputSearch {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    .list-of-data {
    flex-direction: row;
    padding-inline-start: 43px;
    }
    .names {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
    }
    .date {
        text-align: initial;
    }
    
}
